
import rest from '../scripts/restClient';
import Scroller from 'vux/src/components/scroller/index.vue'
import Countup from 'vux/src/components/countup/index.vue'

import {
getShop,
getShopDto
} from '../scripts/global';


export default {
name: 'shoppingMall',
data() {
return {
shopDto: getShopDto(),
point: 0,
dataList: [],
isFirstload: true,
searchModel: {
pageSize: 10,
pageNo: 0,
count: "",
column: "",
orderby: ""
},
loaderStatus: {
pullupStatus: ''
},
pullupConfig: {
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
}
}
},
components: {
Scroller,
Countup
},
methods: {
init() {
var self = this;
this.getList();
this.getPoint();
},
getList(searchModel) {
var self = this;
var searchModel = this.createSearchModel();
rest.post("dragon/pointDetail/search", null, searchModel, function(data) {
self.loaderStatus.pullupStatus = 'default';
self.isFirstload = false; //是否第一次加载
self.resolveList(data);
});
},
getPoint() {
var self = this;
rest.get("dragon/point", null, function(data) {
self.point = data.data.validPoint;
});
},
createSearchModel() {
if (this.isFirstload) {
return this.searchModel;
} else {
return $.extend(this.searchModel, {
pageNo: this.searchModel.pageNo + 1
})
}
},
resolveList(data) {
this.searchModel = $.extend(this.searchModel, {
pageNo: data.pageNo,
pageSize: data.pageSize,
count: data.count
});

$.each(data.data, function(i, item) {
item.add = (item.point > 0);
});
this.dataList = this.dataList.concat(data.data);


if (!data.data.length) {
this.loaderStatus.pullupStatus = 'disabled';
}
}
},
created: function() {
var self=this;

getShop().then(function(data) {
self.shopDto = data;
});

this.init();
}
}



