import rest from '../scripts/restClient';

export default {
  name: 'deliveryRecord',
  data() {
    return {
      entity: {}
    }
  },
  methods: {
    init() {
      var self=this;
      rest.get("dragon/pointRewardOrder", {
        id: this.$route.query.id
      }, function(data) {
         self.entity=data.data;
      })
    }
  },
  created() {
    this.init();
  }
}



