
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV2Data from 'vux/src/datas/china_address_v2.json'
import Value2nameFilter from 'vux/src/filters/value2name.js'
import Group from 'vux/src/components/group/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import rest from '../scripts/restClient';
import {
getShop,
getShopDto
} from '../scripts/global';


export default {
name: 'itemExchange',
data() {
return {
shopDto: getShopDto(),
query: this.$route.query,
addressTitle: '所在地区',
receiverName: "",
receiverMobile: "",
addressData: ChinaAddressV2Data,
addressDto: [],
address: "",
confirm:{show:false},
confirmSuccess:{show:false}
}
},
methods: {
redeem() {
var self = this,
addressData = Value2nameFilter(this.addressDto, this.addressData).split(" "),
postData = {
receiverName: this.receiverName,
receiverMobile: this.receiverMobile,
province: addressData[0],
city: addressData[1],
district: addressData[2],
address: this.address,
pointRewardUuid: this.query.relationUuid,
};
if (this.doValidate(postData)) {
rest.post("dragon/pointReward/redeem", null, postData, function(data) {
self.confirmSuccess.show=true;
});
}
},
doValidate(data){
var toast=this.$vux.toast;
if(!data.receiverName){
toast.show({
text:"收货人不能为空！",
type:"warn"
})
return false;
}else if(!data.receiverMobile){
toast.show({
text:"联系电话不能为空！",
type:"warn"
})
return false;
}else if(!data.province){
toast.show({
text:"所在地区不能为空",
type:"warn"
})
return false;
}else if(!data.address){
toast.show({
text:"详细地址不能为空",
type:"warn"
});
return false;
}
return true;
},
onConfirm(){
location.href=this.shopDto.mallUrl;
}
},
components: {
XAddress,
ChinaAddressV2Data,
Group,
Confirm
},
mounted(){
var self=this;
getShop().then(function(shopDto){
self.shopDto = shopDto;
});

rest.get("dragon/buyer",null,function(data){
console.log(data.data)
var buyer=data.data;
self.receiverName=buyer.name;
self.receiverMobile=buyer.mobile;
self.address=buyer.address;
self.addressDto=[buyer.province,buyer.city,buyer.district];
});
}
}



