
import rest from '../scripts/restClient';
import Vue from 'vue'
import award from '../components/award';
import moment from "moment";
import {
getShopDto,
getShop,
doAuthorize
} from '../scripts/global';
import Confirm from 'vux/src/components/confirm/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Loading from 'vux/src/components/loading/index.vue'



export default {
name: 'itemDetail',
data() {
return {
shopDto: getShopDto(),
entity: {
prize: {}
},
confirm: {
show: false
},
dialog: {
show: false
},
query: this.$route.query,
moment:moment
}
},
components: {
Confirm,
XDialog,
Loading,
award
},
methods: {
init() {
var self = this;
this.$vux.loading.show({
text: '加载中'
})


getShop(self.query).then(function(shopDto) {
self.shopDto = shopDto;

var params=$.extend({},self.query,shopDto);
doAuthorize(params);

rest.get("dragon/pointReward/check", {
relationUuid: self.query.relationUuid
}, function(data) {

});
});

rest.get("common/pointReward", this.query, function(data) {
self.$vux.loading.hide({
text: '加载中'
});
self.entity = data.data;
})
},
redeem() {
var self=this;
rest.post("dragon/pointReward/redeem",null, {
pointRewardUuid: this.query.relationUuid
}, function(data) {
self.dialog.show=true;
})
}
},
mounted() {
this.init();
}
}



