
import Vue from 'vue'
import $jq from "../lib/jquery.min";
import rest from '../scripts/restClient';
import Scroller from 'vux/src/components/scroller/index.vue'

import {
getShop,
doAuthorize,
getShopDto
} from '../scripts/global';

import AlertPlugin from 'vux/src/plugins/alert/index.js'


Vue.use(AlertPlugin);

export default{
components: {},
name: 'browseItem',
data () {
return {
uuid: "", //1c445bcc47b448c2b712afa9d15ecf06
module: "integral",
relationType:"browse_item",
browseItem: {},
itemModels: [],
browseNum: 0,
shopInfo: {
shop: {}
},
buyer: {}
}
},
methods: {
lookRule(){
const self = this;
if (!self.browseItem.rule) {
return;
}
window.application.dialog = {
content: '<div style="text-align: left;">' + self.browseItem.rule.replace(/\n/g, "<br>") + '</div>',
show: true,
link: "",
btnText: "确定",
action(event) {
window.application.dialog.show = false;
}
};
},
browseItemLog(item){
const self = this;
rest.post("dragon/browseItemLog", {relationUuid: self.uuid, itemId: item.id}, null, function (data) {
item.isAdd = true;
window.location.href = "https://item.taobao.com/item.htm?id=" + item.id;
});
},
awardPoint(){
const self = this;
rest.post("dragon/browseItem/awardPoint", {relationUuid: self.uuid}, null, function (data) {
if (!data.data) {
self.$vux.alert.show({
title: '提示',
content: '浏览商品后才能领取积分哦~',
onShow () {
},
onHide () {
}
})
} else {
self.$vux.alert.show({
title: '领取成功',
content: '你领取了' + data.data / 100 + "积分！",
onShow () {
},
onHide () {
}
})
}
});
},
getBrowseItem(){
const self = this;
rest.get("dragon/browseItem", {relationUuid: self.uuid}, function (data) {
self.browseItem = data.data.browseItem;
self.itemModels = data.data.itemModels;
self.browseNum = self.getBrowseNum();
});
},
getBrowseNum(){
var num = 0;
this.itemModels.forEach(function (item) {
num += item.isAdd;
});
return num;
},
getShopInfo() {
const that = this;
getShop().then(function (data) {
that.shopInfo = data;
});
},
doAuthorize() {

const self = this;
const params = $.extend({}, {
uuid: self.uuid,
module: self.module,
relationType: self.relationType
}, self.$route.query);
doAuthorize(params).then(function (buyer) {
self.buyer = buyer;
self.getBrowseItem();
})
},
getIntegralActivity(){
const self = this;
rest.get("common/integral", {relationType: self.relationType, relationUuid: self.uuid}, function (data) {
self.browseItem = data.data.browseItem;
});
},
init() {
const self = this;
self.uuid = self.$route.query.uuid;
self.getIntegralActivity();
getShop().then(function (data) {
self.shopInfo = data;
self.doAuthorize();
});

}
},
mounted() {
const that = this;
this.$nextTick(function () {
that.init();
});
}
}



