
import rest from '../scripts/restClient';
import Scroller from 'vux/src/components/scroller/index.vue'

import {
getShopDto,
getShop,
doAuthorize
} from '../scripts/global';


export default{
name:'shoppingCar',
data () {
return {
searchParam: {    //查询信息
module: "interact",
relationType:"shopping_car",
relationUuid:"ebaa87f69a58422e9565e3ece86eea6c"
},
query: this.$route.query,
activeCenterUrl:'',
shopDto:getShopDto(),    //用户信息
actRuler: {    //活动规则
title: '活动规则',
detail: [

]
},
actDetail: '加购本活动页面中0个宝贝，即可获得xxx！',    //加购获奖规则
prizeInfo: {    //奖品信息
detail: '恭喜您获得xxx',
useMethod: function(){},
methodText: '立即使用',
prizeRecordId: null
},
goodIndex: null,    //被选中宝贝序号
toastPrizeGotIsShow: false,    //奖品已领取
toastCouponGotIsShow: false,    //优惠券已领取
rulerDialogIsShow: false,    //显示活动规则
addDialogIsShow: false,    //显示是否加购Dialog
prizeDialogIsShow:false,   //显示领取奖品Dialog

prizeGot: false,    //奖品是否已领取
minAddNum: 0,    //最少加购宝贝个数
addNum: 0,    //已加购宝贝个数
actStatus: false,    //活动是否有效
goodsList: [    //宝贝列表

]
}
},
computed: {
prizeActive: function () {    //满足领奖条件
if (this.addNum >= this.minAddNum) {
return true;
}
return false;
},
getPrizeBtnText: function () {    //底部按钮文字
return this.prizeGot ? '已领取' : '领取奖品';
},
},
methods: {
init: function(){

var self=this;
self.$vux.loading.show('加载中');
this.getList();    //初始化活动列表











getShop(self.query).then(function(shopDto) {
self.shopDto = shopDto;
self.activeCenterUrl = shopDto.interactUrl;
self.$vux.loading.hide();

var params=$.extend({},self.query,shopDto);
doAuthorize(params).then(function(){
self.check().then(self.activateAct);
});
});
},
getUrlParam: function() {    //获取URL参数
var self = this
return new Promise(function(resolve,reject){
self.searchParam.module = self.query.module;
self.searchParam.relationType = self.query.relationType;
self.searchParam.relationUuid = self.query.relationUuid;
resolve();
})










},
getList: function(){    //初始化活动列表
var self=this;
var searchParam = self.searchParam;

return new Promise (function(resolve,reject){
var action = {
successCallback: function(data) {
var _goodsList = self.goodsList.concat([])
self.goodsList = _goodsList.concat(data.data.shoppingCarModel.itemModels);
self.minAddNum = data.data.shoppingCarModel.shoppingCar.minStoreNum;
self.getActDetailAndPrizeInfo(data);
self.getActRule(data);
resolve ();
},
failCallback: function (data) {
console.log(data);
}
};
rest.get("common/activity", searchParam, action);
})

},
getAddNum: function (data) {    //获取已加购数目
var _addNum = 0;
this.goodsList = data.data.itemModels;
this.goodsList.forEach(function(good){
if (good.isAdd) {
_addNum += 1;
}
})
this.addNum = _addNum;
},
getActDetailAndPrizeInfo: function(data){    //获取活动详情、奖品信息
var self = this,
_minAddNum = this.minAddNum,
_actDetail = '',
_prizeInfo = {
detail: '恭喜您获得xxx',
useMethod: function(){
window.location.href='https://h5.m.taobao.com/app/cpkg/portal.html';
},
methodText: '立即使用',
prizeRecordId: null
},
prizeData = data.data.shoppingCarModel.shoppingCar.prizes[0];

switch(prizeData.type)
{
case 'coupon':
_prizeInfo.useMethod = function () {
var action = {
successCallback: function (data) {
window.location.href= data.data;
self.$vux.toast.show({
text: "操作成功，请在跳出的新页面最下方点击领取优惠券",
position: "center"
})

},
failCallback: function (data) {
console.log(data);
self.toastCouponGotIsShow = true;
setTimeout(function(){
self.toastCouponGotIsShow = false;
},1000)
}
};
rest.get("dragon/prizeRecord", {prizeRecordId: self.prizeInfo.prizeRecordId}, action);
};
_prizeInfo.methodText = '马上领取';
if(prizeData.subType == 'coupon_shop'){
_actDetail = '加购本页面中' + _minAddNum + '个宝贝，即可获得' + prizeData.denomination/100 + '元店铺优惠券！'
_prizeInfo.detail = '恭喜您获得' + prizeData.denomination/100 + '元店铺优惠券！'
}else if(prizeData.subType == 'coupon_goods'){
_actDetail = '加购本页面中' + _minAddNum + '个宝贝，即可获得' + prizeData.denomination/100 + '元商品优惠券！';
_prizeInfo.detail = '恭喜您获得' + prizeData.denomination/100 + '元商品优惠券！'
}
break;
case 'alipay_red':
_actDetail = '加购本页面中' + _minAddNum + '个宝贝，即可获得' + prizeData.denomination/100 + '元支付宝红包！';
_prizeInfo.detail = '恭喜您获得' + prizeData.denomination/100 + '元支付宝红包！';
break;
case 'flow':
_actDetail = '加购本页面中' + _minAddNum + '个宝贝，即可获得' + prizeData.denomination/100 + 'M手机流量包！';
_prizeInfo.detail = '恭喜您获得' + prizeData.denomination/100 + 'M手机流量包！';
break;
case 'point':
_actDetail = '加购本页面中' + _minAddNum + '个宝贝，即可获得' + prizeData.denomination/100 + (self.shopDto.unit||'积分') + '！';
_prizeInfo.detail = '恭喜您获得' + prizeData.denomination/100 + (self.shopDto.unit||'积分') + '！';
_prizeInfo.methodText = '去兑换商品';
_prizeInfo.useMethod = function () {
window.location.href=self.shopDto.mallUrl;
};
break;
case 'ticket':
_actDetail = '加购本页面中' + _minAddNum + '个宝贝，即可获得' + prizeData.denomination/100 + '注彩票！';
_prizeInfo.detail = '恭喜您获得' + prizeData.denomination/100 + '注彩票！';
_prizeInfo.methodText = '去兑换商品';
_prizeInfo.useMethod = function () {
window.location.href=self.shopDto.mallUrl;
};
break;
default:

}

this.actDetail = _actDetail;
this.prizeInfo = _prizeInfo;
},
getActRule: function(data){    //获取活动规则
var actRuleDetail = null,
detailStr = data.data.shoppingCarModel.shoppingCar.rule;

actRuleDetail = detailStr.split(/\n/);
this.actRuler.detail = actRuleDetail;

},
check: function(){    //检查活动是否有效
var self = this;

return new Promise (function(resolve,reject){
var action = {
successCallback: function (data) {
console.log('success',data);
self.prizeGot = data.data.obtained;
self.getAddNum(data);
resolve ();
},
failCallback: function (data) {
console.log('fail',data);
window.location.href='/#/activityMiss';
}
};
rest.get("dragon/shoppingCar/check", {relationUuid: self.searchParam.relationUuid}, action);
})

},
activateAct: function(){    //激活活动
var self = this;
this.actStatus = true;


},
addToCar: function (idx) {    //加购宝贝
this.goodIndex = idx;
if(!!this.goodsList[this.goodIndex].isAdd){
return;
}
this.addDialogIsShow = true;
},
cancelAdd: function(){    //取消加购
this.addDialogIsShow = false;
},
sureAdd: function(idx){    //确定加购
this.goodIndex = idx;
if(!!this.goodsList[this.goodIndex].isAdd){
return;
}
var self = this,
obj = {itemId:self.goodsList[self.goodIndex].id,relationUuid: self.searchParam.relationUuid};

if(this.shopDto.env == "local"){//本地
store();
}else{//淘宝
Tida.ready({}, function () {
Tida.cart({
sellerNick: self.shopDto.shop.ownerNick,
itemId: obj.itemId
},function(data){
if(data.businissType == 0 && data.ret[0] == "HY_SUCCESS"){//ios成功
store();
} else if (data.businissType == 0 && data.ret == "HY_SUCCESS"){//安卓成功
store();
}else if (data.data == "businissType:0" && data.ret == "HY_SUCCESS"){//安卓成功
store();
}else if((data.businissType == 2 || data.businissType == 1) && data.ret[0] == "HY_SUCCESS"){
self.$vux.toast.show({
text: "请操作加入购物车",
position: "center"
})
} else if(data.errorCode == 6 && data.errorMsg == "加入购物车失效"){
self.$vux.toast.show({
text: "购物车已满，请清理后重试",
position: "center"
})
} else{
alert("加入购物车失败，请刷新重试"+JSON.stringify(data));
}
},function (e) {
alert("淘宝组件调用失败，请刷新重试:"+JSON.stringify(e));
})
})
};

function store() {
self.addDialogIsShow = false;
var action = {
successCallback: function (data) {
console.log('success');
self.goodsList[self.goodIndex].isAdd = 1;
self.addNum += 1;
}
};
rest.post("dragon/shoppingCar/doShoppingCar", obj, null, action);
}

},
getPrize: function () {    //获取奖品
var self = this;
if(!this.prizeActive){
this.toastPrizeGotIsShow = true;
var _this = this;
setTimeout(function(){
_this.toastPrizeGotIsShow = false;
},1000)
return;
}else if(this.prizeGot){
return;
}else{
var action = {
successCallback: function (data) {
console.log('success',data);
self.prizeDialogIsShow = true;
self.prizeGot = true;
self.prizeInfo.prizeRecordId = data.data.prizeRecordId;
}
};
rest.post("dragon/shoppingCar/doTakeAward", {relationUuid: self.searchParam.relationUuid}, null, action)

}
},
cancelUsePrize: function () {    //暂不使用奖品
this.prizeDialogIsShow = false;
},
jumpToShop: function () {    //进店逛逛
window.location.href='https://shop' + this.shopDto.shop.outerShopId + '.taobao.com/';
},
jumpToGood: function (idx) {    //查看宝贝
console.log(idx,this.goodsList)
window.location.href='https://item.taobao.com/item.htm?id=' + this.goodsList[idx].id;
},
scroll: function (e) {    //页面滚动
if(e.target.scrollTop > 10) {
$('.shoppingCar>main').css({
top: -3.466666 + 'rem'
})
}
if(e.target.scrollTop == 0) {
$('.shoppingCar>main').css({
top: 0
})
}
},
stop: function (e) {
console.log('click...');
e.stopPropagation();
}

},
created:function(){
var self = this;
this.getUrlParam().then(self.init);

},
}



