
import Vue from 'vue'
import $jq from "../lib/jquery.min";
import restClient from '../scripts/restClient';
import Group from 'vux/src/components/group/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import {getShopDto, getShop, doAuthorize} from '../scripts/global';
export default{
name:'perfectPersonalInfo',
data(){
return {
info:{
name:'',
sex:'',
birthday: this.setToday(),
QQ: '',
micro: '',
phone: '',
email: '',
address: '',
childName:'',
childName: '',
childBirthDay:this.setToday()

},
sex:['男', '女'],
rule:null,
isShowError: false,
errorText:'',
data:'',
activeData:'',
nowDate: '',
hasActive:{
name: false,
sex:false,
birthday: false,
qq: false,
weChat: false,
mobile: false,
email: false,
address: false,
childName:false,
childSex: false,
childBirthday:false,
},
isShow: true,
successIsShow: false,
ruleIsShow: true,
query: this.$route.query,
shopDto:getShopDto()
}
},
methods:{

close: function(type){
if(type == 'success'){
this.successIsShow = false;
this.isShow = false;
}else if(type == 'rule'){
this.ruleIsShow = false;
this.isShow = false;
}
},
save: function(){
if(!this.info.name && this.hasActive.name){
this.isShowError = true;
this.errorText = '请输入您的姓名';
return;
}else if(this.hasActive.name && this.info.name){
this.data.name = this.info.name;
}

if(!this.info.sex && this.hasActive.sex){
this.isShowError = true;
this.errorText = '请输入您的性别';
return;
}else if(this.hasActive.sex && this.info.sex){
this.info.sex == '男' ? this.data.sex = 'male' : this.data.sex = 'female';
}

if(!this.info.birthday && this.hasActive.birthday){
this.isShowError = true;
this.errorText = '请输入您的生日';
return;
}else if(this.hasActive.birthday && this.info.birthday){
this.data.birthday = this.info.birthday;
}

if(!this.info.QQ && this.hasActive.qq){
this.isShowError = true;
this.errorText = '请输入您的QQ号';
return;
}else if(this.hasActive.qq && this.info.QQ){
this.data.qq = this.info.QQ;
}

if(!this.info.micro && this.hasActive.weChat){
this.isShowError = true;
this.errorText = '请输入您的微信号';
return;
}else if(this.hasActive.weChat && this.info.micro){
this.data.weChat = this.info.micro;
}

if(!this.info.phone && this.hasActive.mobile){
this.isShowError = true;
this.errorText = '请输入您的手机号';
return;
}else if(this.hasActive.mobile && this.info.phone){
this.data.mobile = this.info.phone;
}

if(!this.info.email && this.hasActive.email){
this.isShowError = true;
this.errorText = '请输入您的邮箱';
return;
}else if(this.hasActive.email && this.info.email){
this.data.email = this.info.email;
}

if(!this.info.babyName && this.hasActive.childName){
this.isShowError = true;
this.errorText = '请输入宝贝姓名';
return;
}else if(this.hasActive.childName && this.info.babyName){
this.data.childName = this.info.babyName;
}

if(!this.info.babySex && this.hasActive.childSex){
this.isShowError = true;
this.errorText = '请输入宝贝性别';
return;
}else if(this.hasActive.childSex && this.info.babySex){
this.info.babySex == '男' ? this.data.childSex = 'male' : this.data.childSex = 'female';
}

if(!this.info.babyBirthday && this.hasActive.childBirthday){
this.isShowError = true;
this.errorText = '请输入宝贝生日';
return;
}else if(this.hasActive.childBirthday && this.info.babyBirthday){
this.data.childBirthDay = this.info.babyBirthday;
}
var params =  this.data;
var action = {
successCallback: function (data) {
this.isShow = true;
this.successIsShow = true;
}.bind(this),
failCallback: function (data) {
var self = this;
this.isShowError = true;
this.errorText = data.resultMessage;
setTimeout(function(){
self.isShowError = false;
},2000);
}.bind(this)
};
restClient.post("dragon/collectionBuyerInfo/doTakeAward", {relationUuid:"12a688e59115404d97195ad21a5301e4"},params, action);

},
setToday: function(){
var now = new Date();
var cmonth = now.getMonth() + 1;
var day = now.getDate();
if (cmonth < 10) cmonth = '0' + cmonth;
if (day < 10) day = '0' + day;
return now.getFullYear() + '-' + cmonth + '-' + day;

},

getActiveInfo: function(){
var params = {
relationUuid:'12a688e59115404d97195ad21a5301e4'
};
var self = this;
var action = {
successCallback: function (data) {

}.bind(this),
failCallback: function (data) {
alert(data.resultMessage);
}
};
restClient.get("dragon/collectionBuyerInfo/check", params, action);
}
},
mounted: function(){
var self=this;

this.getActiveDate = function(){
var params = {

relationType:'collection_info',//this.relationType,
relationUuid:'12a688e59115404d97195ad21a5301e4'//this.relationUuid
};
var action = {
successCallback: function (data) {
this.activeData = data.data;
var self = this;
var arr = this.activeData.collectionBuyerInfo.collectionInfo + "," + this.activeData.collectionBuyerInfo.extensionInfo;
arr = arr.split(",");
arr.forEach(function(value,index, arr){
self.hasActive[value] = value;
});
this.rule = this.activeData.collectionBuyerInfo.rule.replace(/\n/g, "<br>");
}.bind(this),
failCallback: function (data) {
this.$router.push('/activityMiss');
}.bind(this)
};
var promise = restClient.get("common/activity", params, action);
return promise;
};

this.getData = function(){
restClient.get('dragon/buyer',null,function(data){
this.data = data.data;
console.log(data);
if(!this.data) return;

this.info.name = this.data.name || '';
this.info.sex = this.data.sex || '';
this.info.birthday = this.data.birthday.split(' ')[0] || '';
this.info.QQ = this.data.qq || '';
this.info.micro = this.data.weChat || '';
this.info.phone = this.data.mobile;
this.info.email = this.data.name;
this.info.name = this.data.name;
this.info.babyName = this.data.childName;
this.info.babySex = this.data.childSex == 'male' ? '男' : '女';
this.info.babyBirthday = this.data.childBirthDay;
}.bind(this));
};

this.$nextTick(function init(){
this.getActiveDate();

















this.getData();
});
},
updated: function(){

},
components:{
Group,
PopupRadio,
XInput,
Datetime
}
}





