
import Vue from 'vue';
import $jq from "../lib/jquery.min";
import rest from '../scripts/restClient';
import Scroller from 'vux/src/components/scroller/index.vue'

import {getShop} from '../scripts/global';

export default{
name:'activityRecord',
data(){
return {
pullupConfig: {
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
},
itemList: [],
shopData:{},
count:0,
pageNo:0
}
},
methods:{
getList:function(){
var that = this;
var params = {
pageNo:that.pageNo++,
pageSize:10
};
that.$vux.loading.show("加载中");
rest.post('dragon/activityLog/search',null,params,function(data){
that.count = data.count;
that.itemList = that.itemList.concat(data.data);
that.$vux.loading.hide();

if(that.count==that.itemList.length){   //最后一页时
setTimeout(() => {
that.$refs.record.disablePullup()
}, 100)
}else{
setTimeout(() => {
that.$refs.record.donePullup()
}, 100)
}
})
}
},
mounted:function(){
var that = this;
this.$nextTick(function(){
that.getList();
getShop().then(function(data){
that.shopData = data;
});
})
},
components:{
Scroller
}
}



