import rest from '../scripts/restClient';
import award from '../components/award';
import {
	getShop,
	doAuthorize,
	getShopDto
} from '../scripts/global';
export default {
	name: 'home',
	data() {
		return {
			shopDto: getShopDto(),
			buyer: {},
			goodsList: [],
			itemList: [],
			topBanner: {
				isShow: 1,
				picUrl: ''
			},
			bottomBanner: {
				isShow: 1,
				picUrl: ''
			},
			bgImage: {

				isShow: 0,
				picUrl: ''
			}
		}
	},
	methods: {
		init() {
			var self = this;
			this.getHome();
			this.getGooodsList();
			this.getActivityList();
			
			//获取店铺信息
			getShop().then(function(shopDto) {
				self.shopDto = shopDto;
				
				//遍历背景图片
				self.shopDto.homeSettings.forEach(function(item) {
					if (item.type == 'topBanner') {
						self.topBanner = item;
					}
					if (item.type == 'backgroundImage') {
						self.bgImage = item;
					}
					if (item.type == 'bottomBanner') {
						self.bottomBanner = item;
					}
				})
				//授权
				var params = $.extend({}, shopDto, self.$route.query);
				doAuthorize(params).then(function(buyer) {
					
					self.buyer = buyer;
				})
			});
		},
		getGooodsList() {
			var self = this;
			this.$vux.loading.show("加载中");
			rest.post("common/mall", null, {
				pageSize: 4,
				onShelf: 1,
				pageNo: 0
			}, function(data) {
				self.goodsList = data.data;
				self.$vux.loading.hide();
			});
		},
		getHome(){
		  rest.get("common/home", null, function(data) {

		  });
		},
		getActivityList() {
			var that = this;
			rest.post("common/interact", null, {
				pageSize: 2,
				status: 5,
				pageNo: 0
			}, function(data) {
				that.itemList = data.data;
			});
		},
		gotoLink(link){				
			if(link){
				event.stopPropagation();
				window.location.href = link;					
			}	
		}
	},
	mounted() {
		this.init();
	},
	components: {
		award
	}
}



